.chosen-container {
  font-size: 14px; }

.chosen-container-multi .search-field,
.chosen-container-single .chosen-single,
.chosen-container-active.chosen-with-drop .chosen-single {
  background: #fff url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M5%206l5%205%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%23555%22%2F%3E%3C%2Fsvg%3E) no-repeat right 5px top 55%;
  background-size: 16px 16px;
  border: 1px solid #7e8993;
  box-shadow: 0 0 0 transparent;
  color: #32373c; }

.chosen-container-multi .chosen-choices,
.chosen-container-single .chosen-single {
  border-radius: 4px;
  border-color: #7e8993; }

.chosen-container-multi.chosen-with-drop .chosen-choices,
.chosen-container-active.chosen-with-drop .chosen-single {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04); }

.chosen-container-single.chosen-container-active.chosen-with-drop.chosen-dropup .chosen-single,
.chosen-container-multi.chosen-container-active.chosen-dropup .chosen-choices {
  border-radius: 0 0 4px 4px;
  border-width: 0 1px 0 1px;
  z-index: 1011; }

.chosen-container .chosen-drop {
  position: absolute;
  top: 100%;
  z-index: 1010;
  width: 100%;
  border-radius: 0 0 4px 4px;
  border-width: 0 1px 0 1px;
  border-color: transparent;
  background: #fff;
  outline: 2px solid transparent; }

.chosen-container-single .chosen-single div b {
  background-image: none; }

.chosen-container-single .chosen-search:after {
  display: block;
  position: absolute;
  right: 6px;
  top: 50%;
  font-family: dashicons;
  font-size: 17px;
  content: '\f179';
  transform: translateY(-50%); }

.chosen-container-single.chosen-container-active.chosen-with-drop .chosen-single {
  border-radius: 4px 4px 0 0; }

.chosen-container-single .chosen-single div {
  width: 26px; }

.chosen-container-single .chosen-default {
  color: #32373c; }

.chosen-container-active .chosen-single {
  border-color: transparent;
  outline: 2px solid transparent; }

.chosen-container-single .chosen-search input[type=text] {
  margin: 1px 0;
  padding: 4px 20px 4px 5px;
  width: 100% !important;
  height: auto;
  outline: 0;
  border: 1px solid #7e8993;
  border-radius: 4px;
  line-height: normal;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.07); }

@media screen and (max-width: 782px) {
  .chosen-container-single .chosen-single,
  .chosen-container-single.chosen-container-active.chosen-with-drop .chosen-single {
    font-size: 16px;
    line-height: 1.625;
    min-height: 40px;
    padding: 5px 8px; } }

.chosen-container-multi .search-field {
  border: none; }

.chosen-container-multi.chosen-with-drop .chosen-choices {
  border-bottom-color: transparent; }

.chosen-container-multi .chosen-choices li.search-field {
  min-width: 100px !important;
  width: 100%; }

.chosen-container-multi .chosen-choices li.search-field input[type=text] {
  color: #32373c;
  font-family: unset;
  height: unset;
  margin: 0;
  padding: 3px 10px;
  width: 100% !important; }

.chosen-container-multi .spinner {
  position: absolute;
  top: 4px;
  right: -4px; }

.chosen-container-multi .chosen-choices li.search-choice {
  margin: 3px 5px 3px 0;
  padding: 5px 22px 5px 5px;
  border: 1px solid #7e8993;
  max-width: 100%;
  border-radius: 4px;
  background: #f4f4f4;
  box-shadow: none;
  color: #32373c;
  cursor: default; }

.chosen-container-multi .chosen-choices li.search-choice .search-choice-close {
  position: absolute;
  top: 4px;
  right: 3px;
  display: block;
  width: 15px;
  height: 15px; }

.chosen-container-multi .chosen-choices li.search-choice .search-choice-close:before {
  height: 15px;
  width: 15px;
  position: absolute;
  top: 0;
  right: 0;
  color: #32373c;
  font-family: 'dashicons';
  content: '\f158';
  font-size: 15px;
  line-height: 1; }

.chosen-container-multi .chosen-choices li.search-choice .search-choice-close:hover:before {
  color: #444; }

.chosen-container .chosen-results {
  color: #444;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0 4px 4px 0;
  padding: 0 0 0 4px;
  max-height: 240px; }

.chosen-container .chosen-results li.highlighted {
  background-image: none;
  border-radius: 4px;
  color: #fff; }

